
  @media all {
    footer{
    box-sizing: border-box;
    background-color: black;
    width: 100%;
    max-height: 209px;
    color: white;
    font-size: 24px;
    line-height: 142.6%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 28px;
    margin-top: 50px;
    padding-top:50px;
    padding-bottom: 30px;
  }

    .footer__img{
        width: 122px;
        height: 39.44px;}

        .footer__copy{
            font-family: "Montserrat", Arial, "Sans-serif";
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 142.6%;}
  }

  @media (max-width: 600px) {
    .footer__copy{
    font-size: 16px;
  }
}

  @media (max-width: 375px) {
    footer{
      row-gap: 40px;
      padding-bottom:62px;
    }

    .footer__copy{
    font-size: 12px;
    margin-top: 20px;
  }
}